.modal {
  opacity: 0;
  visibility: hidden;
  box-sizing: border-box;
  width: 335px;
  max-height: 95%;
  z-index: 30;
  background-color: #fff;
  border: 2px solid #111;
  border-radius: 18px;
  margin-bottom: 0;
  padding: 40px 24px;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
}

